/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import styled from '@emotion/styled';
import { graphql } from 'gatsby';
import Page from 'src/layout/Page';
import SEO from 'components/seo';
import { Container, Row, Col, Button } from 'react-bootstrap';
import { MagazineContents } from 'src/models';
import { Color } from 'src/constants';
import Markdown from 'src/components/Markdown';

export const pageQuery = graphql`
  query($path: String!) {
    mdx(frontmatter: { path: { eq: $path } }) {
      body
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        path
        title
      }
    }
  }
`;

interface Props {
  data: {
    mdx: MagazineContents;
  }
}
const Magazine = ({ data }: Props) => {
  const { mdx } = data;
  if(!mdx) {
    return <div>Render Error</div>;
  }

  const { frontmatter, body } = mdx;

  return (
    <Page pageName={frontmatter.title} showHead={false}>
      <Container data-type="markdown" css={css`
        max-width: 800px;
        margin: 3rem auto;
      `}>
        <Row>
          <Col xs={12} sm={12} md={12} lg={12}>
            <TitleArea>
              <Title>{frontmatter.title}</Title>
              <Date>{frontmatter.date}</Date>
            </TitleArea>
            <Markdown contents={body} />
          </Col>
        </Row>
        <Row css={css`
          margin-top: 3rem;
        `}>
          <Col>
            <BottomCTA variant="secondary" href="/">홈으로</BottomCTA>
          </Col>
        </Row>
      </Container>
    </Page>
  );
};

const TitleArea = styled.div`
  border-bottom: 1px solid ${Color.BlueGrey100};
  margin-bottom: 2rem;
  padding-bottom: 2rem;
`;
const Title = styled.h1`
  font-weight: 700;
  color: ${Color.BlueGrey900};
`;
const Date = styled.p`
  font-size: 1rem;
  color: ${Color.BlueGrey800};
  margin: 0;
  font-family: serif;
`;
const BottomCTA = styled(Button)`
  width: 100%;
`;

export default Magazine;
